import { useEffect, useState } from "react";
import Contacto from "./components/Contacto";
import { obtenerTodosContactos } from "./functions/function.js";
import Insertar from "./components/Insertar";
import Actualizar from "./components/Actualizar";
import "./styles/index.css";
import Login from "./components/Login";
import Logout from "./components/Logout";
import { useAuth0 } from "@auth0/auth0-react";
import { useToast, Toast } from "toastify-simply-react";

function App() {
	const [refrescar, setRefrescar] = useState(false);
	const [state, setState] = useState([]);
	const { user } = useAuth0();
	const [mostrarInsertar, setMostrarInsertar] = useState(false);
	const [mostrarActualizar, setMostrarActualizar] = useState(false);
	const [contactoEditar, setContactoEditar] = useState({});
	const [toast, toastRef] = useToast();

	useEffect(() => {
		obtenerTodosContactos(setState);
	}, [refrescar, mostrarInsertar, mostrarActualizar]);

	const showEdit = (id, nombre, numero) => {
		setMostrarActualizar(!mostrarActualizar);
		setContactoEditar({ id, nombre, numero });
	};

	console.log(user);
	return (
		<>
			<div className="container_log_btns">
				<Login className="login" />
				<Logout className="logout" />
			</div>
			<div className="container__title">
				<h1>Lista de contactos</h1>
				<button
					className="btn_newContact"
					onClick={() => {
						if (user) {
							console.log("El usuario si existe");
							setMostrarInsertar(!mostrarInsertar);
						} else {
							// console.log("El usuario NO existe");
							toast.error("Para poder Crear un contacto necesita iniciar sesion", { timeout: "1100" });
						}
					}}
				>
					Agregar contacto
					<img className="img-btn" src={process.env.PUBLIC_URL + "/img/iconContact.svg"} alt="add_contact" />
				</button>
			</div>
			<Toast ref={toastRef} />

			<div className="padre_contenedor">
				{mostrarInsertar ? <Insertar setMostrarInsertar={setMostrarInsertar}></Insertar> : ""}
				{mostrarActualizar ? <Actualizar contactoEditar={contactoEditar} setMostrarActualizar={setMostrarActualizar}></Actualizar> : ""}

				{state.length === 0 ? (
					<h2 className="loading">Cargando...</h2>
				) : state ? (
					state.map(e => {
						return <Contacto showEdit={showEdit} state={e} key={e.id_contactos} setRefrescar={setRefrescar} refrescar={refrescar} />;
					})
				) : (
					`No existen contactos`
				)}
			</div>
		</>
	);
}

export default App;
