import React from "react";
import ReactDOM from "react-dom";
import "./styles/index.css";
import App from "./App";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import { Auth0Provider } from "@auth0/auth0-react";
import Login from "./components/Login";

const name_domain = process.env.REACT_APP_NAME_DOMAIN;
const client_id = process.env.REACT_APP_CLIENT_ID;
ReactDOM.render(
	<Auth0Provider domain={name_domain} clientId={client_id}>
		<BrowserRouter>
			<Routes>
				<Route path="/FrontEnd-Proyecto-Contactos-/" element={<App />} />
				<Route path="/FrontEnd-Proyecto-Contactos-/login" element={<Login />} />
			</Routes>
		</BrowserRouter>
	</Auth0Provider>,
	document.getElementById("root")
);
