import "../styles/estilos.css";
import { borrarContacto } from "../functions/function";
import { useAuth0 } from "@auth0/auth0-react";
import { useToast, Toast } from "toastify-simply-react";

function Contacto({ state, showEdit, setRefrescar, refrescar }) {
	const { user } = useAuth0();
	const [toast, toastRef] = useToast();

	const borrar = referesco => {
		borrarContacto(state.id_contactos);
		setRefrescar(!referesco);
	};

	return (
		<div className="contenedor" key={state.id_contactos}>
			<div className="contenedor_info">
				<img alt={state.nombre_contacto} src={process.env.PUBLIC_URL + "/img/contact.png"} />
				<div className="container_info-text">
					<p className="text_name">{state.nombre_contacto}</p>
					<p className="text_number">{state.numero_contacto}</p>
				</div>
			</div>
			<div className="container_button">
				<button
					className="btns"
					onClick={() => {
						if (user) {
							// console.log("El usuario si existe");
							showEdit(state.id_contactos, state.nombre_contacto, state.numero_contacto);
						} else {
							// console.log("El usuario NO existe");
							toast.error("Para poder Actualizar necesita iniciar sesion", { timeout: "1100" });
						}
					}}
				>
					Actualizar
					<img src={process.env.PUBLIC_URL + "/img/iconEdit.svg"} alt="edit" />
				</button>

				<button
					className="btns"
					onClick={() => {
						if (user) {
							borrar(refrescar, state.id_contactos);
						} else {
							toast.error("Para poder Eliminar necesita iniciar sesion", { timeout: "1000" });
						}
					}}
				>
					Eliminar
					<img src={process.env.PUBLIC_URL + "/img/iconDelete.svg"} alt="delete" />
				</button>
			</div>
			<Toast ref={toastRef} />
		</div>
	);
}

export default Contacto;
